/* Most CSS rules are in JS components */
body {
  margin: 0;
  padding: 0;
  font-family:
    'Roboto',
    'Open Sans',
    -apple-system,
    BlinkMacSystemFont,
    Segoe UI,
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    Fira Sans,
    Droid Sans,
    Helvetica Neue,
    sans-serif;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.main-content-container {
  flex-grow: 1;
  position: relative;
}
#map-container {
  height: 100vh;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #333;
}
.mapboxgl-popup-content > .popup > * {
  margin: 0;
}
.map-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0;
  pointer-events: none;
}
.map-overlay-pane {
  display: block;
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
